* {
	user-select: none;
	outline: none;
	-webkit-tap-highlight-color: transparent;
	text-decoration: none;
}

body {
	background-color: #FFF;
	margin: 0px;
	overflow: hidden;
}

.canvas {
	box-shadow: inset 0px 0px 160px 90px rgba(0,0,0,0.5);
	transition: background 0.2s;
	box-shadow: inset 0px 0px 0px 0px rgba(0,0,0,0.5);
	box-shadow: 0px 0px 30px rgba(0,0,0,0.2);
}

.configPanel {
	width: calc(100vw - 70vh);
	height: calc(100vh);
	position: fixed;
	left: 70vh;
	top: 0px;
	background-color: #111;
}

.itemsPanel  {
	height: 100px;
	white-space: nowrap;
}

.itemsPanel > .item {
	height: 70px;
	width: 70px;
	display: inline-block;
	background-size: 60px auto;
	margin: 13px 0px 13px 13px;
	border-radius: 8px;
	background-repeat: no-repeat;
	background-position: center;
	border: 2px solid #fff2;
	cursor: pointer;
	transition: all 0.2s;
}


.itemsPanel > .itemOpened {
	border: 2px solid #06c;
	background-color: #06c2;
}

.sectBar {
	height: 40px;
	background-color: #fff2;
	color: #999;
	line-height: 40px;
	padding: 0px 10px;
}

.itemName {
	height: 145px;
	width: 94px;
	display: inline-block;
	line-height: 10px;
	text-align: center;
	font-size: 12px;
	color: #999;
	margin-left: 12px;
	overflow: hidden;
	vertical-align: top;
}

.itemName > .item {
	height: 90px;
	width: 90px;
	display: inline-block;
	overflow: hidden;
	vertical-align: top;
	margin: 12px 0px 12px 0px;
	border-radius: 8px;
	cursor: pointer;
	background-size: 150% auto;
	border: 2px solid #000;
	box-shadow: 0px 3px 4px rgba(0,0,0,0.1);
}

.itemName > .itemOpened {
	border: 2px solid #06c;
}

.itemName > .itemOpened::before {
	content: '.';
	display: inline-block;
	height: 90px;
	width: 90px;
	background-color: #0008;
	background-image: url("data:image/svg+xml,<svg id='svg' viewBox='0 0 16 16' xmlns='http://www.w3.org/2000/svg' style='transform: rotate(0deg); fill: rgba(255, 255, 255, 0.69);'><path d='M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z'></path></svg>");
	background-position: center;
	background-repeat: no-repeat;
	background-size: 40px auto;
	color: transparent;
}

.loading {
	position: fixed;
	width: 60px;
	height: 60px;
	background-image: url(bin/load.svg);
	background-repeat: no-repeat;
	background-position: center;
	background-size: 50px auto;
	top: calc(50vh - 30px);
	left: calc(35vh - 30px);
}

@media (orientation: portrait) {
	.configPanel {
		width: 100vw;
		height: calc(100vh - 70vw - 50px);
		position: fixed;
		left: 0px;
		top: 70vw;
		overflow-y: auto;
	}
	.itemName > .item {
		height: 60px;
		width: 60px;
	}
	.itemName > .itemOpened::before {
		height: 60px;
		width: 60px;
		background-size: 20px auto;
	}
	.canvas {
		background-size: 30px auto !important;
	}
	.itemName {
		height: 115px;
		width: 74px;
		margin-left: 8px;
	}
	.arimac_logo {
		display: none !important;
	}

	.loading {
		position: fixed;
		width: 60px;
		height: 60px;
		background-image: url(bin/load.svg);
		background-repeat: no-repeat;
		background-position: center;
		background-size: 50px auto;
		top: calc(35vw - 30px);
		left: calc(50vw - 30px);
	}

}